:root {
  --header-height: 132px;
  --section-spacing: 20px;
  --radius: 0.625rem;
}
html {
  scroll-behavior: smooth;
  scroll-padding-top: var(--header-height);
}
body {
  background-color: #ffffff;
  color: #0a0a0a;
  font-family: Arial, Helvetica, sans-serif;
}
* {
  box-sizing: border-box;
}
section {
  position: relative;
  padding: var(--section-spacing) 0;
  scroll-margin-top: var(--header-height);
}
.hero-section {
  min-height: 100vh;
  min-height: 100dvh;
  padding-top: calc(var(--header-height) + var(--section-spacing));
  contain: layout style paint;
  will-change: transform;
}
.section-inner {
  padding-top: var(--section-spacing);
  padding-bottom: var(--section-spacing);
}
.section-wrapper {
  margin-top: var(--section-spacing);
  margin-bottom: var(--section-spacing);
}
.bg-section-red {
  background: linear-gradient(180deg, rgba(179, 25, 66, 0.06), rgba(179, 25, 66, 0.03));
}
.bg-section-blue {
  background: linear-gradient(180deg, rgba(10, 49, 97, 0.06), rgba(10, 49, 97, 0.03));
}
.bg-section-stars {
  background-color: rgba(255, 255, 255, 0.6);
  background-image: radial-gradient(circle at 10px 10px, rgba(179, 25, 66, 0.25) 1px, transparent 1.5px),
    radial-gradient(circle at 30px 25px, rgba(10, 49, 97, 0.25) 1px, transparent 1.5px);
  background-size: 40px 40px, 60px 60px;
}
@media (prefers-color-scheme: dark) {
  .bg-section-red {
    background: linear-gradient(180deg, rgba(179, 25, 66, 0.12), rgba(179, 25, 66, 0.06));
  }
  .bg-section-blue {
    background: linear-gradient(180deg, rgba(10, 49, 97, 0.12), rgba(10, 49, 97, 0.06));
  }
  .bg-section-stars {
    background-color: rgba(20, 20, 20, 0.2);
    background-image: radial-gradient(circle at 10px 10px, rgba(179, 25, 66, 0.35) 1px, transparent 1.5px),
      radial-gradient(circle at 30px 25px, rgba(10, 49, 97, 0.35) 1px, transparent 1.5px);
  }
}
.about-title {
  white-space: nowrap;
  line-height: 1.15;
  font-size: clamp(1rem, 2.2vw + 0.25rem, 2.75rem);
  letter-spacing: -0.01em;
}
.line-clamp-4 {
  display: -webkit-box;
  overflow: hidden;
  line-clamp: 4;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}
.nav-link-underline {
  position: relative;
  display: inline-block;
}
.nav-link-underline::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  height: 2px;
  width: 100%;
  transform: scaleX(0);
  transform-origin: left;
  background-color: currentColor;
  transition: transform 200ms ease-in-out;
}
.nav-link:hover .nav-link-underline::after,
.nav-link-underline[data-active='true']::after {
  transform: scaleX(1);
}
.hero-section video {
  contain: layout style paint;
  will-change: transform;
}
img {
  content-visibility: auto;
  contain-intrinsic-size: 300px;
}
.custom-scrollbar {
  scrollbar-width: thin;
  scrollbar-color: #0a3161 #f1f5f9;
}
.custom-scrollbar::-webkit-scrollbar {
  width: 10px;
}
.custom-scrollbar::-webkit-scrollbar-track {
  background: #f1f5f9;
  border-radius: 5px;
}
.custom-scrollbar::-webkit-scrollbar-thumb {
  background-color: #0a3161;
  border-radius: 5px;
  border: 1px solid #002868;
}
.custom-scrollbar::-webkit-scrollbar-thumb:hover {
  background-color: #002868;
  border: 1px solid #001a4d;
}
@keyframes marquee {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(calc(-100% - var(--gap)));
  }
}
@keyframes marquee-vertical {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(calc(-100% - var(--gap)));
  }
}
[data-allow-animation] .animate-marquee {
  animation: marquee var(--duration, 40s) linear infinite;
}
[data-allow-animation] .animate-marquee-vertical {
  animation: marquee-vertical var(--duration, 40s) linear infinite;
}
